import React from 'react';
import { Row } from 'antd';
import { connect } from "react-redux";
import { formUploadItem } from "./form-item-config";
import Avatar from "../components/Avatar/Avatar";
import Select from "../components/Select/Select";
// import axios from "axios";

class FormContent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hospital: "angiang",
      content: ""
    }
  }

  componentDidMount = () => {

  }

  changeHospital = (value) => {
    this.setState({
      hospital: value
    })
  }

  // onFinish = values => {
  //   console.log('Success:', values);
  // };

  // onFinishFailed = errorInfo => {
  //   console.log('Failed:', errorInfo);
  // };



  showAvatar = () => {
    const result = formUploadItem.map((item, index) => {
      return <Avatar key={index} label={item.label} name={item.name} hospital={this.state.hospital} />
    })

    return result
  }

  render() {

    return (
      <div>
        <Select changeHospital={this.changeHospital} default={this.state.hospital} />
        <br />
        <br />

        < Row >
          {this.showAvatar()}

        </Row >
      </div >

    );
  }
}


// const mapStateToProps = state => {

// };

// const mapDispatchToProps = dispatch => ({


// });


export default connect(null, null)(FormContent);